import React from 'react';
import Aux from "../../hoc/Aux";
import "./Generated.css";
const Generated = (props) => {
return(
    <div className="Generated">
        <div className="box">
            {props.show?<Aux><h4>https://redirecto.herokuapp.com/{props.shortURL}</h4>
            <h4>{props.code}</h4></Aux>:<h3>Get Your Shortened Link With Analytics</h3>}
        </div> 
    </div>
);
};
export default Generated;


