import React, { Component } from 'react';
import "./Analysis.css";
import axios from 'axios';
import Loader from "../../../components/Loader/Loader";
import {
    XYPlot,
    XAxis,
    YAxis,
    HorizontalGridLines,
    VerticalGridLines,
    VerticalRectSeries
} from 'react-vis';

let dataa = []
// const URL = "http://localhost:4000";
const URL = "https://redirecto.herokuapp.com";
class Analysis extends Component {
    state = {
        visitors: [],
        url: '',
        token: '',
        loading: false,
        error:null
    }
    onFormChanger = (event) => {
        this.setState({
            [event.target.name]: event.target.value
        })

    }
    onFormSubmit = (e) => {
        e.preventDefault();
        this.setState({
            loading: true
        })
        let kk = this.state.url.split('/').slice(-1)[0]
        if (this.state.url.trim() !== '' || this.state.token.trim() !== '')
            axios.post(URL+"/visits", { url: kk, token: this.state.token })
                .then(resp => {

                    var modData = resp.data.map((e) => {
                        return {
                            country: e.country,
                            date: new Date(e.updated)
                        }
                    })
                    this.setState({
                        visitors: modData,
                        loading: false
                    })
                }).catch(err=>{
                    console.log(err);
                    
                    this.setState({
                        error:err,
                        loading: false
                    })
                })
    }
    formatDate = (value) => {
        return value.toLocaleDateString('en-US');
    }

    genrateBins = () => {
        dataa = [];
        let onlyDates = this.state.visitors.map(val => (val.date))
        onlyDates.sort();
        for (var i = 0; i < onlyDates.length; i++) {
            if (i === 0)
                dataa.push({ x: onlyDates[i], y: 1 })
            else {
                console.log(this.formatDate(dataa[dataa.length - 1].x) + " " + this.formatDate(onlyDates[i]));

                if (this.formatDate(dataa[dataa.length - 1].x) !== this.formatDate(onlyDates[i])) {
                    dataa.push({ x: onlyDates[i], y: 1 })
                }
                else {
                    dataa[dataa.length - 1].y++;
                }
            }
        }
        console.log(dataa);

    }
    generateBars = () => {
        let ONE_DAY = 86400000 / 1.5;
        const DATA = dataa.map((el, i) => ({ x0: el.x.getTime() + ONE_DAY * (i + 1) - (ONE_DAY), x: el.x.getTime() + ONE_DAY * (i + 1), y: el.y }));
        console.log(DATA);

        return DATA
    }
    // [
    // { x0: 1, x: 2, y: 1 },
    // ]
    render() {
        let timestamp = new Date().getTime();
        let ONE_DAY = 86400000;
        return (
            <div className="Analysis" >
                <form onSubmit={this.onFormSubmit}>
                    <input type="text" name="url" placeholder="Enter Your Link" onChange={this.onFormChanger} value={this.state.url} />
                    <input type="number" name="token" placeholder="Enter Your Code" onChange={this.onFormChanger} value={this.state.token} />
                    <button >Get Analysis</button>
                </form>
                {this.state.visitors.length !== 0 ? this.genrateBins() : null}
                {/* knjkn */}
    {  this.state.visitors.length !==0 ?(         
         <div className="Graph">
                    <XYPlot
                        xDomain={[timestamp - 15 * ONE_DAY, timestamp + 15 * ONE_DAY]}
                        xType="time"
                        width={1000}
                        height={300}>
                        <HorizontalGridLines />
                        <VerticalGridLines />


                        <XAxis title="X Axis" position="start" bottom={0}
                            style={{
                                line: { stroke: '#ADDDE1' },
                                ticks: { stroke: '#ADDDE1' },
                                text: { stroke: 'none', fill: '#6b6b76', fontWeight: 600 }
                            }}
                        />
                        <YAxis title="Y Axis" color="white" style={{
                            line: { stroke: '#ADDDE1' },
                            ticks: { stroke: '#ADDDE1' },
                            text: { stroke: 'none', fill: '#6b6b76', fontWeight: 600 }
                        }} />
                        {/* <LineSeries
                            color="white"
                        data={[...this.state.visitors.map(val => { return { x: val.date, y: 1 } })]} /> */}
                        <VerticalRectSeries data={this.generateBars()} style={{ stroke: '#fff' }} />
                        {/* <MarkSeries data={[...this.state.visitors.map(val => { return { x: val.date, y: 1 } })]} /> */}

                    </XYPlot>


                </div>
    ):null}
                    {this.state.visitors.length !== 0 ? this.state.visitors.map((val, i) => (<li key={i}>{val.country}({this.formatDate(val.date)})</li>)) : null}
    {this.state.loading ? <Loader /> : null}
                {this.state.error?<p style={{color:"white"}}>Network Error (You Entered Something Wrong!)</p>:null}
            </div>
        );
    }
};
export default Analysis;


