import React, { Component } from 'react';
import Navbar from '../../components/Navbar/Navbar';
import InputForm from '../../components/InputForm/InputForm';
import "./Main.css";
import axios from "axios";
import Aux from "../../hoc/Aux";
import Generated from '../../components/Generated/Generated';
import { Route, Switch } from "react-router-dom";
import Analysis from './Analysis/Analysis';
import Loader from '../../components/Loader/Loader';
import Desp from '../../assets/description.png'
// const URL="http://localhost:4000";
const URL = "https://redirecto.herokuapp.com";
class Main extends Component {
    state = {
        url: '',
        shorturl: '',
        code: '',
        loading:false,
        err:null
    }
    onFormChanger = (event) => {
        this.setState({
            [event.target.name]: event.target.value
        })

    }
    onFormSubmit = (e) => {
        e.preventDefault();
        if(this.state.url.trim()!==''){
            this.setState({
                loading: true
            })

        axios.post(URL + "/short", { url: this.state.url })
            .then(res => {
                this.setState({
                    shorturl: res.data.url,
                    code: res.data.token,
                    loading:false
                })
            }).catch(err=>{
                this.setState({
                    err:err,
                    loading:false
                })
            })
        }

    }

    render() {
        let app = (
            <Aux><form onSubmit={this.onFormSubmit}>
                <InputForm value={this.state.url} formChanger={this.onFormChanger} />
                <button class="btn-gen">Generate</button>
            </form>
                <Generated shortURL={this.state.shorturl} code={this.state.code} show={this.state.shorturl !== ''} />
                </Aux>)

        let appt = (
            < Switch >
            <Route path="/analysis" component={Analysis} />
            <Route path="/" render={() => (app)} />
                </Switch >
               )
        return (
            <div>
                <Navbar />
               {this.state.err?"ERROR":appt}
                {this.state.loading ? <Loader /> : null}
            </div>
        );
    };
}
export default Main;


